body.iqit-front-editor {
  overflow: hidden;
  background-color: #525252;
}

.mce-container *{
  white-space: normal !important;
}


*, :after, :before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

#iqitfronteditor-tools {
  background: #e6e6e6;
  padding-right: 4px;
  position: fixed;
  width: 300px;
  height: 100%;
  top: 0;
  box-shadow: 2px 1px 4px rgba(0, 0, 0, 0.15);
  bottom: 0;
  overflow: visible;
  left: 0px;
  z-index: 2;
  -webkit-transition: left .3s;
  transition: left .3s;

  &._closed-panel {
    left: -300px;
  }

  #iqitfronteditor-tools-panels {
    overflow: hidden;
    margin-bottom: 100px;
    bottom: 0px;
    top: 0px;
    position: absolute;
    width: 100%;
  }

  #iqitfronteditor-tools-trigger {
    position: absolute;
    left: 300px;
    top: 50%;
    width: 17px;
    height: 40px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    box-shadow: 2px 1px 4px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    z-index: 22;
    border: none;
    background: #e6e6e6;
    outline: 0;
    transition: ease 0.4s;
    &:hover {
      background: #fff;
    }
    i {
      transition: ease 0.4s;
    }
    &._closed-panel {
      i {
        transform: rotate(180deg);
      }
    }
  }

  .iqit-config-tab-heading {
    background: #fff;
    border-bottom: 1px solid #e6e6e6;
    padding: 12px 14px;
    margin-bottom: 10px;
    font-weight: bold;
    position: relative;
    padding-left: 50px;
    font-size: 110%;
    height: 45px;
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
  }

  .iqit-config-tab-close {
    border: none;
    background: none;
    width: 40px;
    height: 44px;
    position: absolute;
    top: 0px;
    left: 0px;
    color: #555555;
    text-align: center;
    border-right: 1px solid #e6e6e6;
    -webkit-transition: all .3s;
    transition: all .3s;
    outline: none !important;

    &:hover{
      background: #fbfbfb;
    }
    i {
      font-size: 24px;
    }
  }

  .iqit-config-tab-heading-main{
    padding-right: 50px;
    padding-left: 15px;

    .iqit-config-tab-close{
    top: 0px;
    right: 0px;
    left: auto;
    border-left: 1px solid #e6e6e6;
    border-right: none;
    line-height: 49px;
    }
  }

  #iqitfronteditor-tools-bottom {
    position: absolute;
    bottom: 0px;
    background: #fff;
    width: 100%;
    padding: 10px 12px;
    border-top: 1px solid #e6e6e6;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.1);

    .preview-selector{
      border-bottom: 1px solid #e6e6e6;
      padding-bottom: 10px;
      margin-bottom: 11px;
      #preview-page{
        display: inline-block;
        width: auto;
        margin-left: 5px;
      }
    }

    .tools{
      overflow: hidden;
    }

    .responsive-buttons {
      margin-top: 6px;
    }
    .responsive-buttons button {
      border: none;
      background: none;
      outline: none;
      i {
        font-size: 16px;
      }
    }

    .responsive-buttons button.active {
      color: #25B9D7;
    }

    #iqitfronteditor-save {
      color: #fff;
      background-color: #78D07D;
      border: none;
      padding: 5px 14px;
      border-radius: 3px;
      float: right;
      -webkit-transition: all .3s;
      transition: all .3s;

      &:hover {
        background-color: #70B580;
      }

      i {
        margin-right: 4px;
      }

      &._saved{
        background: #cecece !important;
        opacity: .6;
      }
    }
    #iqitfronteditor-save-success{
      float: right;
      padding: 5px 10px;
      color: #78D07D;
      -webkit-transition: all .3s;
      transition: all .3s;
      opacity: 0;
      visibility: hidden;
      &._saved{
        visibility: visible;
        opacity: 1;
      }
    }
    #iqitfronteditor-save-false{
      width: 100%;
      position: absolute;
      left: 100%;
      z-index: 2;
      bottom: 0;
    }
  }
}

#iqitfronteditor-exit-modal{
  -webkit-transition: all .3s;
  transition: all .3s;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0px;
  left: 0px;
  visibility: hidden;
  opacity: 0;
  z-index: 22;

  &._visible{
    visibility: visible;
    opacity: 1;
  }

  #iqitfronteditor-exit-modal-content{
    background: #fff;
    width: 400px;
    height: auto;
    padding: 30px;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);

    .modal-tile{
      font-size: 130%;
      font-weight: bold;
      display: block;
      margin-bottom: 20px;
    }
  }

  #iqitfronteditor-modal-back{
    color: #fff;
    background-color: #C45C67;
    border: none;
    padding: 5px 14px;
    border-radius: 3px;
    -webkit-transition: all .3s;
    transition: all .3s;
    text-decoration: none;
  }

  #iqitfronteditor-modal-close {
    background: none;
    border: none;
    padding: 5px 14px;
  }

}

#iqit-config-pans {
  list-style: none;
  display: block;
  padding: 0;
  margin: 0;
  position: relative;
  -webkit-transition: ease 0.4s;
  -moz-transition: ease 0.4s;
  -o-transition: ease 0.4s;
  transition: ease 0.4s;
  height: 100%;
  overflow-x: hidden;

  ul, li {
    list-style: none;
    display: block;
    padding: 0;
    margin: 0;
  }

  .iqit-config-tab {
    background: #fff;
    border-bottom: 1px solid #e6e6e6;
  }

  .iqit-config-tab-title {
    cursor: pointer;
    padding: 12px 14px;
    display: block;
    position: relative;

    i {
      font-size: 20px;
      position: absolute;
      right: 12px;
      top: 10px;
    }
  }

  .iqit-config-tab-group {
    width: 100%;
    background: #e6e6e6;
    position: absolute;
    height: 100%;
    top: 0;
    bottom: 0;
    right: -100%;
    -webkit-transition: ease 0.4s;
    -moz-transition: ease 0.4s;
    -o-transition: ease 0.4s;
    transition: ease 0.4s;

    &._opened-group {
      right: 0;
      opacity: 1;
      z-index: 2;
      overflow-x: hidden;
      //overflow: inherit;
    }
  }
}

#iqit-config-pans::-webkit-scrollbar {
  width: 8px
}

#iqit-config-pans::-webkit-scrollbar-track {
  background-color: #eaeaea;
  border-left: 1px solid #ccc
}

#iqit-config-pans::-webkit-scrollbar-thumb {
  background-color: #888888
}

#iqit-config-pans::-webkit-scrollbar-thumb:hover {
  background-color: #aaa
}

#iqit-config-fieldsets {

  .hidden-option {
    display: none;
  }
  .visible-option {
    display: block;
  }
  .visible-inline-option {
    display: inline-block;
  }

  .iqit-config-fieldset {
    width: 100%;
    background: #e6e6e6;
    position: absolute;
    height: 100%;
    top: 0;
    bottom: 0;
    right: -100%;
    -webkit-transition: ease 0.4s;
    -moz-transition: ease 0.4s;
    -o-transition: ease 0.4s;
    transition: ease 0.4s;
    overflow: auto;

    &._opened-fieldset {
      -webkit-transform: translateX(-300px);
      -moz-transform: translateX(-300px);
      -ms-transform: translateX(-300px);
      -o-transform: translateX(-300px);
      transform: translateX(-300px);
      opacity: 1;
      z-index: 2;
    }
  }

  .iqit-config-fieldset::-webkit-scrollbar {
    width: 8px
  }
  .iqit-config-fieldset::-webkit-scrollbar-track {
    background-color: #eaeaea;
    border-left: 1px solid #ccc
  }
  .iqit-config-fieldset::-webkit-scrollbar-thumb {
    background-color: #888888
  }
  .iqit-config-fieldset::-webkit-scrollbar-thumb:hover {
    background-color: #aaa
  }
}

._opened-panels {
  overflow: hidden;
}

#iqitfronteditor-preview {
  left: 300px;
  position: absolute;
  height: 100%;
  top: 0;
  right: 0;
  border: 0;
  -webkit-transition: all .3s;
  transition: all .3s;

  &._phone-preview {
    width: 380px;
    margin: 20px auto;
    border: 17px solid #000;
    border-radius: 20px;
    height: 95%;
  }

  &._tablet-preview {
    width: 788px;
    margin: 20px auto;
    border: 17px solid #000;
    border-radius: 20px;
    height: 95%;
  }

  #iqitfronteditor-iframe {
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 0 none;
  }

  &._full-width {
    left: 0px;
  }
}

#iqitfronteditor-preview-loader, #iqitfronteditor-tools-loader {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  position: absolute;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all .3s;
  transition: all .3s;
  z-index: 3;

  .loader {
    width: 35px;
    height: 35px;
    position: absolute;
    left: 0px;
    right: 0px;
    margin: auto;
    top: 0px;
    bottom: 0px;
    border: 6px solid rgb(159, 224, 237);
    border-top: 6px solid #25b9d7;
  }
}

#iqitfronteditor-tools-loader {
  background: rgba(255, 255, 255, 0.5);
}

#iqitfronteditor-tools.loading-tools {
  #iqitfronteditor-tools-loader {
    visibility: visible;
    opacity: 1;
  }
}

#iqitfronteditor-preview.loading-preview {
  #iqitfronteditor-preview-loader {
    visibility: visible;
    opacity: 1;
  }
}

.iqit-front-editor {
  .form-wrapper {
    background: #fff;
    padding: 12px;
  }

  .form-group {
    margin-bottom: 10px;
  }

  .form-control, input[type="text"], input[type="search"], input[type="password"], textarea, select {
    display: block;
    width: 100%;
    height: 28px;
    padding: 4px 8px;
    font-size: 12px;
    line-height: 1.42857;
    color: #555;
    background-color: #efefef;
    background-image: none;
    border: none;
    -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  label.control-label {
    font-size: 13px;
    color: #000;
    display: block;
    padding-bottom: 8px;
  }

  .help-block {
    font-size: 80%;
    opacity: 0.8;
    margin: 0px;
    margin-top: 4px;
  }

  .input-group-addon {
    padding: 0px 8px;
    font-size: 12px;
    line-height: 1.42857;
    font-weight: normal;
    color: #555;
    text-align: center;
    background-color: #e6e6e6;
    border-left: 1px solid #d8d8d8;
    vertical-align: middle;
  }

  .input-group {
    position: relative;
    display: table;
    border-collapse: separate;
  }

  .input-group .form-control, .input-group input[type="text"], .input-group input[type="search"], .input-group input[type="password"], .input-group textarea, .input-group select {
    position: relative;
    z-index: 2;
    float: left;
    width: 100%;
    margin-bottom: 0;
    margin-top: 0px;
  }

  .input-group-addon, .input-group-btn, .input-group .form-control, .input-group input[type="text"], .input-group input[type="search"], .input-group input[type="password"], .input-group textarea, .input-group select {
    display: table-cell;
  }

  .colorpicker-component {
    margin-left: 0px !important;
  }

  hr {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: -12px;
    margin-right: -12px;
    border: 0;
    border-top: 1px solid #e6e6e6;
  }

  .title-reparator {
    background: #e6e6e6;
    padding: 10px 12px;
    margin-left: -12px;
    margin-right: -12px;
    font-size: 13px;

  }

  .box-shadow-generator .boxshadow-control {
    margin-right: 6px;
  }

  .js-border-generator .col-xs-2 {
    display: inline-block;
    vertical-align: top;
  }
  .border-generator{
      .border-width{
        width: 65px;
        display: inline-block;
        vertical-align: top;
      }

      .border-color{
        vertical-align: top;
        display: inline-block;
       width: 110px;
      }
  }
  .js-font-size.width-150 {
    width: 70px !important;
  }

  .alert{
    padding: 12px;
    font-size: 90%;
    position: relative;

    .iqit-close-warning{
      position: absolute;
      outline: none;
      border: none;
      background: none;
      top: 7px;
      right: 3px;
      cursor: pointer;
    }
    ul{
      margin: 0px;
      padding: 0px 30px;
    }
  }


  .alert-info {
    background-color: #DCF4F9;
    color: #1e94ab;

  }

  .alert-warning {
    background-color: #FDE7BB;
    color: #7d683e;
    margin-bottom: 5px;
  }

  .alert-danger {
    background: #C45C67;
    color: #fff;
  }

  .background-wrapper .visible-option {
    display: inline-block !important;
  }
  .prestashop-switch {
    input[type="checkbox"], input[type="radio"] {
      vertical-align: middle;
    }
    label {
      margin-right: 8px;
    }
  }

  .subtitle-reparator {
    margin-bottom: 5px;
    font-size: 13px;
    text-decoration: underline;
  }

  .js-iframe-upload {
    text-decoration: none;
    font-size: 90%;
    color: #3d3d3d;
    i {
      vertical-align: middle;
    }
  }

}





